@import url(https://fonts.googleapis.com/css?family=Roboto);
.popup{padding:0 8px;max-width:160px}.popup .name{font-size:14.5px;font-weight:600;margin-bottom:8px}.popup .distance,.popup .elevation{font-size:13px;font-weight:400}.popup .distance label,.popup .elevation label{color:#666;width:60px;font-size:12px;font-weight:400}.popup .github-button{margin:12px -2px 6px -2px}

.side-bar{width:280px;padding:16px 0;background:#fff;overflow:auto}.side-bar h2{margin:0;padding:0 26px;margin-bottom:8px}.side-bar .route{border-bottom:1px solid #eaeaea;padding:16px 26px;cursor:pointer}.side-bar .route:hover,.side-bar .route.is-selected{background:#f5f5f5}.side-bar .route .name{font-size:17px;font-weight:600;margin-bottom:8px}.side-bar .route .distance,.side-bar .route .elevation{font-size:15px;font-weight:400}.side-bar .route .distance label,.side-bar .route .elevation label{color:#666;width:60px;font-size:13px;font-weight:400}.side-bar .route .actions{margin-top:16px}.is-phone .side-bar,.is-tablet .side-bar{flex-direction:row !important;width:100%;padding:0}.is-phone .side-bar h2,.is-tablet .side-bar h2{display:none}.is-phone .side-bar .route,.is-tablet .side-bar .route{border-bottom:none;width:260px;height:160px}.is-phone .side-bar .route:not(:last-child),.is-tablet .side-bar .route:not(:last-child){border-right:1px solid #eaeaea}.is-phone .side-bar .route:hover,.is-tablet .side-bar .route:hover{background:unset}.is-phone .side-bar .route.is-selected,.is-tablet .side-bar .route.is-selected{background:#f5f5f5}.is-phone .side-bar .route .actions,.is-tablet .side-bar .route .actions{margin-top:auto}

.explorer{height:100%}.is-phone .explorer,.is-tablet .explorer{flex-direction:column-reverse !important}

.details{padding:16px 100px 0 100px;overflow:auto}.details .markdown{color:#222;padding-bottom:48px;min-height:300px}.details .markdown .wrapper{max-width:1200px}.details .markdown .actions{position:absolute;top:31px;right:0;padding-left:24px}.details .markdown .actions .button-inner{height:28px}.details .markdown .remarkable{padding-right:32px}.details .markdown .remarkable img{height:0;width:0;visibility:hidden}.details .markdown .remarkable h1{font-weight:500;font-size:36px;display:inline-block}.details .markdown .remarkable p{font-size:16px;margin:0;margin-bottom:4px;max-width:calc(1200px - 300px - 64px);line-height:1.3}.details .markdown .summary>div:not(:last-child){margin-right:24px}.details .markdown .summary label{color:#666;font-size:14px;font-weight:400}.details .markdown .summary span{font-size:26px;font-weight:500}.details .markdown .images{width:30%}.details .markdown .images img{margin-top:32px;height:200px}.details .map-wrapper{margin:0 -100px;height:600px}.is-phone .details,.is-tablet .details{padding:16px 16px 0 16px}.is-phone .details .markdown,.is-tablet .details .markdown{padding-bottom:16px}.is-phone .details .markdown .wrapper,.is-tablet .details .markdown .wrapper{flex-direction:column !important}.is-phone .details .markdown .wrapper .actions,.is-tablet .details .markdown .wrapper .actions{position:unset;padding-left:0;margin-left:auto;margin-bottom:-24px}.is-phone .details .markdown .wrapper .images,.is-tablet .details .markdown .wrapper .images{width:unset;margin-top:28px;justify-content:flex-start !important}.is-phone .details .markdown .wrapper .images img,.is-tablet .details .markdown .wrapper .images img{margin-top:0;height:100px}.is-phone .details .map-wrapper,.is-tablet .details .map-wrapper{margin:0 -16px;height:400px}

:focus{outline:none}.focused>*{outline:-webkit-focus-ring-color auto 5px;outline-color:#204f7f}*{transition:background-color 200ms}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

html{font-family:'Source Sans Pro', sans-serif;font-size:14px;font-weight:400;color:#000;-webkit-font-smoothing:antialiased}

